<template>
  <v-card>
    <v-card-text>
      <div class="d-flex flex-wrap justify-center">
        <v-card class="ma-1" v-for="(classmate,index) in classmates" :key="index">
          <v-card-text>
            <v-avatar
              :color="classmate.leave?'red':classmate.present?'blue': 'grey'"
              size="70"
              @click="changeStatus(classmate)"
            >
              <v-icon dark x-large>mdi-account-circle</v-icon>
              <div v-if="classmate.leave" class="text-center text-body-1 white--text">請假</div>
            </v-avatar>
          </v-card-text>
          <v-card-text>{{ classmate.name }}</v-card-text>
        </v-card>
      </div>
    </v-card-text>
    <v-card-text>
      <div class="text-h6">點名紀錄</div>
      <v-list>
        <v-list-item
          v-for="(record,index) in records.slice(0, 5)"
          :key="index"
        >{{ record.time }} {{ record.action }}</v-list-item>
      </v-list>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  data() {
    // let todayDate = new Date().toISOString().split("T")[0];

    return {
      className: "小熊搖籃幼幼班",
      classmates: [
        { name: "寶寶1", present: true },
        { name: "寶寶2", present: false },
        { name: "寶寶3", present: true },
        { name: "寶寶4", present: false, leave: true },
        { name: "寶寶5", present: true },
        { name: "寶寶6", present: true },
        { name: "寶寶7", present: false },
        { name: "寶寶8", present: true },
        { name: "寶寶9", present: true },
        { name: "寶寶10", present: false, leave: true },
        { name: "寶寶11", present: true },
        { name: "寶寶12", present: true },
      ],
      records: [],
    };
  },
  methods: {
    changeStatus(classmate) {
      if (classmate.leave) return;
      classmate.present = !classmate.present;
      let time = new Date().toLocaleTimeString({}, { hour12: false });

      this.records.unshift({
        time: time,
        action: `${classmate.name} ${classmate.present ? "到校" : "未到校"}`,
      });
    },
  },
};
</script>

